import React from 'react';
import './Exchange.scss';

import _ from 'lodash';

// import clsx from "clsx";
import classNames from 'classnames';

// import moment from "moment";
import BigNumber from 'bignumber.js';
import commaNumber from 'comma-number';
import LinearProgress from '@material-ui/core/LinearProgress';

// import { makeStyles } from "@material-ui/core/styles";
// import Table from "@material-ui/core/Table";
// import TableBody from "@material-ui/core/TableBody";
// import TableCell from "@material-ui/core/TableCell";
// import TableHead from "@material-ui/core/TableHead";
// import TableRow from "@material-ui/core/TableRow";
// import Paper from "@material-ui/core/Paper";
// import LinearProgress from "@material-ui/core/LinearProgress";
// import TableSortLabel from "@material-ui/core/TableSortLabel";
// import Typography from "@material-ui/core/Typography";
// import Grid from "@material-ui/core/Grid";

// import Button from "@material-ui/core/Button";
// import ButtonGroup from "@material-ui/core/ButtonGroup";
import { TVChartContainer } from '../../components/TVChartContainer';

import CoinSwitch from './components/CoinSwitch';
import OrderBook from './components/OrderBook';
import MarketTrades from './components/MarketTrades';

import useStyles from './styles';

import {
  getPrice,
  findTokens,
  findMarketMetrics,
  findBuyBook,
  findSellBook,
  findTradesHistory,
} from '../../service/SteemEngineService';

const Exchange = props => {
  // console.log('props', props);
  const classes = useStyles();

  const [steemPrice, setSteemPrice] = React.useState(0);
  const [sbdPrice, setSbdPrice] = React.useState(0);

  const [tokens, setTokens] = React.useState([]);
  const [sellBook, setSellBook] = React.useState([]);
  const [buyBook, setBuyBook] = React.useState([]);
  const [tradesHistory, setTradesHistory] = React.useState([]);
  const [selectToken, setSelectToken] = React.useState('');

  const [tokenInfo, setTokenInfo] = React.useState();
  const [loading, setLoading] = React.useState(false);
  const [selectTradesTab, setSelectTradesTab] = React.useState(0);

  React.useEffect(() => {
    // https://postpromoter.net/api/prices
    getPrice().then(_price => {
      setSteemPrice(_price.steem_price);
      setSbdPrice(_price.sbd_price);
    }); //
  }, []);

  React.useEffect(() => {
    if (selectToken) {
      setLoading(true);
      const opts = {
        query: { symbol: selectToken },
      };
      Promise.all([
        findBuyBook(opts), //  
        findSellBook(opts), //  
        findTradesHistory(opts), //  
      ]).then(([_buyBook, _sellBook, _tradesHistory]) => {
        // console.log({ _buyBook, _sellBook, _tradesHistory });
        const totalSellBook = _.sum(_sellBook.map(e => Number(e.quantity)));
        // console.log('totalSellBook', totalSellBook);
        const resultSellBook = _sellBook
          .map(e => {
            const total = new BigNumber(e.quantity).times(e.price).toNumber();
            const quantity = parseFloat(e.quantity);
            return {
              // account: e.account,
              // expiration: e.expiration,
              price: parseFloat(e.price),
              quantity,
              symbol: e.symbol,
              // timestamp: e.timestamp,
              // txId: e.txId,
              total,
              rate: (quantity / totalSellBook) * 100,
            };
          })
          .sort((a, b) => b.price - a.price);
        const totalBuyBook = _.sum(_buyBook.map(e => Number(e.quantity)));
        // console.log('totalBuyBook', totalBuyBook);
        const resultBuyBook = _buyBook
          .map(e => {
            const total = new BigNumber(e.quantity).times(e.price).toNumber();
            const quantity = parseFloat(e.quantity);
            return {
              // account: e.account,
              // expiration: e.expiration,
              price: parseFloat(e.price),
              quantity,
              // symbol: e.symbol,
              // timestamp: e.timestamp,
              // txId: e.txId,
              total,
              rate: (quantity / totalBuyBook) * 100,
            };
          })
          .sort((a, b) => b.price - a.price);
        const resultTradesHistory = _tradesHistory
          .map(e => {
            return {
              // account: e.account,
              type: e.type,
              symbol: e.symbol,
              price: parseFloat(e.price),
              quantity: parseFloat(e.quantity),
              timestamp: e.timestamp,
              volume: e.volume,
            };
          })
          .sort((a, b) => b.price - a.price);
        setSellBook(resultSellBook);
        setBuyBook(resultBuyBook);
        setTradesHistory(resultTradesHistory);
        setLoading(false);
      });
    }
  }, [selectToken]);

  return (
    <React.Fragment>
      <div className={classNames('page-global-exchange', 'page')}>
        <div className="content-top">
          <div className="left">
            <CoinSwitch
              {...props}
              steemPrice={steemPrice}
              selectToken={selectToken}
              setSelectToken={setSelectToken}
              setTokenInfo={setTokenInfo}
            />
          </div>
          <div className="right">
            <div className="l">
              <div className={classNames('mod', 'trading-chart')}>
                <div className="mod-head">
                  {selectToken && (
                    <div className="ticker">
                      <span stop="1" className="symbol-name">
                        {selectToken}/STEEM
                      </span>
                      <div growing-ignore="true" className="price-container">
                        <span className="price color-up">
                          {tokenInfo.lastPrice} STEEM
                        </span>
                        <span className="estimate">
                          {' '}
                          {new BigNumber(tokenInfo.lastPrice)
                            .times(steemPrice)
                            .toFixed(6)}{' '}
                          USD
                        </span>
                      </div>
                      <dl growing-ignore="true" className="change">
                        <dt></dt>
                        <dd
                          className={`color-${
                            tokenInfo.priceChangePercent.startsWith('-')
                              ? 'down'
                              : 'up'
                          }`}
                        >
                          {tokenInfo.priceChangePercent}
                        </dd>
                      </dl>
                      <dl growing-ignore="true" className="high">
                        <dt>24H Ask</dt>
                        <dd>{tokenInfo.lowestAsk} STEEM</dd>
                      </dl>
                      <dl growing-ignore="true" className="low">
                        <dt>24H Bid</dt>
                        <dd>{tokenInfo.highestBid} STEEM</dd>
                      </dl>
                      <dl growing-ignore="true" className="amount">
                        <dt>24H </dt>
                        <dd>
                          {tokenInfo.volume} STEEM / $
                          {commaNumber(
                            new BigNumber(tokenInfo.volume)
                              .times(steemPrice)
                              .toFixed(2),
                          )}
                        </dd>
                      </dl>
                    </div>
                  )}
                </div>
                <div className={classNames('mod', 'mod-body')}>
                  <TVChartContainer />
                </div>
              </div>
            </div>
            <div className={classNames('r', 'trades-container')}>
              <div className="global-trades">
                <ul>
                  <li
                    className={classNames({ cur: selectTradesTab === 0 })}
                    onClick={() => setSelectTradesTab(0)}
                  >
                    
                  </li>
                  <li
                    className={classNames({ cur: selectTradesTab === 1 })}
                    onClick={() => setSelectTradesTab(1)}
                  >
                    
                  </li>
                </ul>
                <div>
                  <OrderBook
                    sellBook={sellBook}
                    symbol={selectToken}
                    steemPrice={steemPrice}
                    buyBook={buyBook}
                    loading={loading}
                    selectedTab={selectTradesTab === 0}
                  />
                  <MarketTrades
                    steemPrice={steemPrice}
                    symbol={selectToken}
                    loading={loading}
                    tradesHistory={tradesHistory}
                    selectedTab={selectTradesTab === 1}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Exchange;
